@import url('https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: 'Space Mono', monospace;
  -webkit-font-smoothing: antialiased;
  background-color: rgb(156,255,206);
  height: 100vh;
}

/* Containers */
.App-Container{
  background-color:white;
  height: 23rem;
  width: 45rem;
  padding: 20px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0px 0px 10px 5px rgba(124, 124, 120, 0.295);
}
#out{
  background-color: rgb(64, 184, 124);
  width: 18rem;
  margin-left: 20px;
  border-radius: 20px;
  overflow: hidden;
  padding: 20px;
}
.input-con{
  padding: 10px;
}

/* inputs */

#custome{
  width: auto;
  height: auto;
  margin-top: 0px;
  padding-right: 0px;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
}
.in{
  height: 30px;
  width: 20rem;
  border-radius: 9px;
  border: none;
  background: rgba(212, 211, 211, 0.568);
  text-align: right;
  padding-right: 20px;
  font-family: 'Space Mono', monospace;
  font-size: 25px;
}
.icon{
  position: absolute;
  left: 20px;
  top: 5px;
}

/* outputs */
#out h2{
  text-align: right;
}

/* buttons */
button{
  height: 40px;
  font-family: 'Space Mono', monospace;
  background-color: rgb(38, 141, 90);
  border-radius: 10px;
  border: none;
  font-weight: 700;
  font-size: 20px;
  color: white;
}
#res{
  margin-top: 5rem;
  width: 15.2rem;
  background-color: white;
  color: black;
}
#res:hover{
  background-color: rgb(156,255,206);
}
.bsel{
  background-color: rgb(8, 87, 76);
  color: rgb(156,255,206);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
