body{
    display: flex;
    align-items: center;
    justify-content: center;
}
.App-Container{
    display: flex;
    flex-direction: row;
}

/* Templates Input*/
.input-con{
    display: grid;
    grid-template-columns: repeat(3, 8rem);
    grid-template-rows: repeat(7, 1fr);
    grid-template-areas: "tbill  tbill tbill"
                        "in in in"
                        "tTip tTip tTip"
                        "b1 b2 b3"
                        "b4 b5 cus"
                        "tp tp tp"
                        "ip ip ip";
    grid-gap: 5px;
}
#out{
    display: grid;
    grid-template-columns: repeat(2, 7.5rem);
    grid-template-rows: repeat(2, 5rem);
    align-items: center;
    justify-content: center;
}

/* Titles */
#tBill{
    grid-area: tbill; 
}
#tST{
    grid-area: tTip;
}
#tNP{
    grid-area: tp;
}

/* Buttons */
#custome{
    grid-area: cus;
}
#b5{
    grid-area: b1;
}
#b10{
    grid-area: b2;
}
#b15{
    grid-area: b3;
}
#b20{
    grid-area: b4;
}
#b25{
    grid-area: b5;
}

/* Inputs */
#inBs{
    margin-top: -10px;
    grid-area: in;
    position: relative;
}
#inPs{
    margin-top: -10px;
    grid-area: ip;
    position: relative;
}

@media (max-width : 799px){
    .input-con{
        display: flex;
        flex-direction: column;
    }
    .App-Container{
        flex-direction: column-reverse;
        height: 90vh;
        width: 70vw;
        padding-top: 1rem;
        padding-bottom: 0;
        justify-content:left;
    }
    #out{
        width: auto;
        margin-left: 0px;
        grid-template-columns: repeat(2, 5rem);
        grid-gap: 15px;
    }
    #res{
        margin-top: 0rem;
        width: 10rem;
    }
    .in{
        width: 100%;
        padding-right: 0;
    }
    button{
        height: auto;
    }
    
}